<template>
  <div class="channel">
    <van-row>
      <van-col
        span="12"
        v-for="(item, index) in channels"
        :key="index"
        @click="handlerChannel(item.tagId,item.id)"
      >
        <img :src="item.bgPicture" alt />
        <span>#{{item.name}}</span>
      </van-col>
    </van-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      channels: []
    };
  },
  methods: {
    getChannel() {
      this.axios
        .get(
          "http://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v4/categories/"
        )
        .then(response => {
          this.channels = response.data;
          // window.console.log(response.data);
        });
    },
    handlerChannel(tagId, id) {
      this.$router.push({
        path: "channelContent",
        query: { tagId: tagId, id: id }
      });
    }
  },
  created() {
    this.getChannel();
  }
};
</script>

<style lang="less" scoped>
.channel {
  .van-col {
    margin-bottom: 0.2rem;
    position: relative;
    text-align: center;
    padding: 0 0.1rem;
    img {
      vertical-align: middle;
    }
    span {
      display: inline-block;
      position: absolute;
      width: 100%;
      line-height: 180px;
      top: 0;
      left: 0;
      color: #fff;
      font-size: 1rem;
    }
  }
}
</style>