<template>
  <div class="videoSmallCard">
    <li @click="routerToPlay">
      <div class="image">
        <img :src="item.data.cover.detail" alt />
        <div class="totaltime">
          <span>{{computingTime(item.data.duration)}}</span>
        </div>
      </div>
      <div class="text">
        <h5>{{item.data.title}}</h5>
        <p class="aa">
          <span class="aa">#{{item.data.category}}</span>
          <span>/</span>
          <span>{{item.data.author.name}}</span>
        </p>
      </div>
    </li>
  </div>
</template>

<script>
export default {
  props: ["item"],
  methods: {
    computingTime(time) {
      var m = parseInt(time / 60);
      var s = parseInt(time % 60);
      if (s < 10) {
        return m + ":0" + s;
      } else {
        return m + ":" + s;
      }
    },
    routerToPlay() {
      this.$router.push("/play");
      this.$store.commit("setCurrentPlay", this.item);
    }
  }
};
</script>

<style lang="less">
.videoSmallCard {
  li {
    margin-top: 5vw;
    padding: 0 5vw;
    display: flex;
    .image {
      width: 40vw;
      position: relative;
      img {
        vertical-align: middle;
      }
      .totaltime {
        position: absolute;
        right: 3%;
        bottom: 5%;
        background: rgba(0, 0, 0, 0.7);
        color: white;
        padding: 2.5px 5px;
        font-size: 12px;
        border-radius: 5px;
      }
    }

    .text {
      flex: 1;
      margin-left: 5vw;
      h5 {
        font-weight: 600;
      }
      span {
        font-size: 14px;
        color: gray;
        margin-right: 10px;
      }
    }
  }
}
</style>