<template>
  <div class="month">
    <ul>
      <VideoList v-for="(item, index) in monthranking" :key="index" :item="item"></VideoList>
    </ul>
  </div>
</template>

<script>
import VideoList from "@/components/VideoList.vue";

export default {
  components: {
    VideoList
  },
  data() {
    return {
      monthranking: []
    };
  },
  methods: {
    getMouthRanking() {
      let monthranking = JSON.parse(window.localStorage.getItem("monthranking"));
      if (monthranking && Date.now() < monthranking.time) {
        this.monthranking = monthranking.data;
      }
      this.axios
        .get(
          "http://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v4/rankList/videos?strategy=monthly&amp;start=0&amp;num=10"
        )
        .then(response => {
          this.monthranking = response.data.itemList;
          window.console.log(response.data);
          window.localStorage.setItem(
            "monthranking",
            JSON.stringify({
              time: Date.now() + 30 * 60 * 1000,
              data: this.monthranking
            })
          );
        });
    }
  },
  created() {
    this.getMouthRanking();
  }
};
</script>

