<template>
  <div class="ranking">
    <div class="title">
      <h2>本周榜单</h2>
      <div class="icon" @click="$router.push({path:'allrank'})">
        <span>查看全部</span>
        <van-icon name="arrow" />
      </div>
    </div>
    <section v-for="(item, index) in ranking" :key="index" :item="item">
      <template v-if="item.type=='videoSmallCard'">
        <ul>
          <VideoSmallCard :item="item"></VideoSmallCard>
        </ul>
      </template>
    </section>
  </div>
</template>

<script>
import VideoSmallCard from "@/components/VideoSmallCard.vue";

export default {
  components: {
    VideoSmallCard
  },
  data() {
    return {
      ranking: []
    };
  },
  methods: {
    getRanking() {
      let ranking = JSON.parse(window.localStorage.getItem("ranking"));
      if (ranking && Date.now() < ranking.time) {
        this.ranking = ranking.data;
      }
      this.axios
        .get(
          "http://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v7/index/tab/discovery?vc=580&vn=6.1&deviceModel=MI%20MAX%202"
        )
        .then(response => {
          this.ranking = response.data.itemList;
          // window.console.log(response.data);
          window.localStorage.setItem(
            "ranking",
            JSON.stringify({
              time: Date.now() + 30 * 60 * 1000,
              data: this.ranking
            })
          );
        });
    }
  },
  created() {
    this.getRanking();
  }
};
</script>

<style lang="less" scoped>
.ranking {
  .title {
    position: relative;
    h2 {
      font-size: 24px;
      font-weight: 600;
      margin-left: 5vw;
      margin-top: 30px;
      margin-bottom: 20px;
    }
    .icon {
      color: #4689d7;
      position: absolute;
      right: 5%;
      font-size: 18px;
      top: 30%;
      .van-icon {
        vertical-align: middle;
      }
    }
  }
}
</style>