      loading: false,
      finished: false
    };
  },
  methods: {
    getVideoList() {
      let videolist = JSON.parse(window.localStorage.getItem("videolist"));
      if (videolist && Date.now() < videolist.time) {
        this.videolist = videolist.data;
      }
      this.axios
        .get(
          "http://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v5/index/tab/feed?vc=580&vn=6.1&deviceModel=MI%20MAX%202"
        )
        .then(response => {
          this.videolist = response.data;
          this.nextPic = response.data.nextPageUrl;
          // window.console.log(response.data);
          window.localStorage.setItem(
            "videolist",
            JSON.stringify({
              time: Date.now() + 10 * 60 * 1000,
              data: this.videolist
            })
          );
        });
    },
    getNextPageUrl() {
      // var url =
