<template>
  <div class="home">
    <van-sticky>
      <ul class="nav">
        <li>
          <router-link to="/ranking">榜单</router-link>
          <van-icon name="coupon-o" class="left" @click="showPoster" />
        </li>
        <li>
          <router-link to="/recommend">推荐</router-link>
        </li>
        <li>
          <router-link to="/channel">频道</router-link>
          <van-icon name="search" class="right" @click="$router.push({path:'search'})" />
        </li>
      </ul>
    </van-sticky>
    
    <van-overlay
      v-if="dayCover"
      class="poster"
      :show="isShowPoster"
      @click="isShowPoster = false"
      :style="{backgroundImage:'url('+dayCover.image_blurred+')'}"
    >
      <div class="wrapper">
        <van-icon class="close" name="cross" @click.stop="isShowPoster = false" />
        <div class="thumb">
          <img :src="dayCover.image" alt />
          <div class="date"></div>
        </div>
        <div class="bottom">
          <div class="intro">
            <h3>{{dayCover.title}}</h3>
            <p>{{dayCover.content}}</p>
          </div>
          <van-icon name="envelop-o" class="share" />
        </div>
      </div>
    </van-overlay>

    <keep-alive>
      <router-view></router-view>
    </keep-alive>
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "home",
  data() {
    return {
      isShowPoster: false,
      dayCover: null
    };
  },
  methods: {
    showPoster() {
      this.isShowPoster = true;
    },
    getDayCover() {
      this.axios
        .get(
          "http://api.kele8.cn/agent/https://app.vmovier.com/apiv3/DayCover/getDayCover"
        )
        .then(response => {
          // window.console.log(response);
          this.dayCover = response.data.data;
        });
    }
  },
  created() {
    this.getDayCover();
  }
};
</script>

