<template>
  <div class="videolist">
    <li v-if="item.data.content">
      <div class="play">
        <img :src="item.data.content.data.cover.detail" alt @click="routerToPlay" />
        <div class="totaltime">
          <span>{{computingTime(item.data.content.data.duration)}}</span>
        </div>
      </div>
      <div class="introduce">
        <img :src="item.data.content.data.author.icon" alt />
        <div class="text">
          <h5>{{item.data.content.data.title}}</h5>
          <p>
            <span>{{item.data.content.data.author.name}}</span>
            <span>/</span>
            <span>#{{item.data.content.data.category}}</span>
          </p>
        </div>
      </div>
    </li>
    <li v-else>
      <div class="play">
        <img :src="item.data.cover.detail" alt  @click="routerToPlay"/>
        <div class="totaltime">
          <span>{{computingTime(item.data.duration)}}</span>
        </div>
      </div>
      <div class="introduce">
        <img :src="item.data.author.icon" alt />
        <div class="text">
          <h5>{{item.data.title}}</h5>
          <p>
            <span>{{item.data.author.name}}</span>
            <span>/</span>
            <span>#{{item.data.category}}</span>
          </p>
        </div>
      </div>
    </li>
  </div>
</template>

<script>
export default {
  props: ["item"],
  methods: {
    computingTime(time) {
      var m = parseInt(time / 60);
      var s = parseInt(time % 60);
      return m + ":" + s;
    },
    routerToPlay() {
      this.$router.push("/play");
      this.$store.commit("setCurrentPlay", this.item);
      window.console.log("xxx");
      
    }
  }
};
</script>

<style lang="less" scoped>
.videolist {
  li {
    border: 1px solid lightgray;
    padding: 5vw;
    list-style: none;
    .play {
      position: relative;
      .totaltime {
        position: absolute;
        right: 3%;
        bottom: 5%;
        background: rgba(0, 0, 0, 0.7);
        color: white;
        padding: 2.5px 5px;
        font-size: 12px;
        border-radius: 5px;
      }
    }
    .introduce {
      display: flex;
      overflow: hidden;
      height: 50px;
      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
      }
      .text {
        margin-left: 10px;
        flex: 1;
        h5 {
          font-size: 18px;
          font-weight: 600;
          display: -webkit-box;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
        p {
          margin-top: 5px;
          span {
            color: gray;
            margin-right: 10px;
          }
        }
      }
    }
  }
}
</style>