<template>
  <div class="play">
    <div class="back" @click="$router.go(-1)">
      <van-icon name="arrow-left" />
    </div>
    <div
      class="playbg"
      :style="{backgroundImage:'url('+currentPlay.data.content.data.cover.blurred+')'}"
      v-if="currentPlay.data.content"
    >
      <div class="content">
        <van-sticky>
          <video
            :src="currentPlay.data.content.data.playUrl"
            controls
            :poster="currentPlay.data.content.data.cover.detail"
          ></video>
        </van-sticky>
        <div class="intro">
          <h3>{{currentPlay.data.content.data.title}}</h3>
          <p>
            <span>#{{currentPlay.data.content.data.category}}</span>
            <span>/</span>
            <span>{{getTime(currentPlay.data.header.time)}}</span>
            <span>/</span>
            <span>开眼精选</span>
          </p>
          <h6>{{currentPlay.data.content.data.description}}</h6>
        </div>
        <div class="author">
          <img :src="currentPlay.data.content.data.author.icon" alt />
          <div class="text">
            <h3>{{currentPlay.data.content.data.title}}</h3>
            <p>{{currentPlay.data.content.data.description}}</p>
          </div>
          <button class="btn">+关注</button>
        </div>
        <div class="relate" v-for="(item, index) in videorelatedmsg.itemList" :key="index">
          <template v-if="item.type=='videoSmallCard'">
            <VideoSmallCard :item="item"></VideoSmallCard>
          </template>
        </div>
      </div>
    </div>

    <div class="playbg" :style="{backgroundImage:'url('+currentPlay.data.cover.blurred+')'}" v-else>
      <div class="content">
        <van-sticky>
          <video :src="currentPlay.data.playUrl" controls :poster="currentPlay.data.cover.detail"></video>
        </van-sticky>
        <div class="intro">
          <h3>{{currentPlay.data.title}}</h3>
          <p>
            <span>#{{currentPlay.data.category}}</span>
            <span>/</span>
            <span>{{getTime(currentPlay.data.releaseTime)}}</span>
            <span>/</span>
            <span>开眼精选</span>
          </p>
          <h6>{{currentPlay.data.description}}</h6>
        </div>
        <div class="author">
          <img :src="currentPlay.data.author.icon" alt />
          <div class="text">
            <h3>{{currentPlay.data.title}}</h3>
            <p>{{currentPlay.data.description}}</p>
          </div>
          <button class="btn">+关注</button>
        </div>
        <div class="relate" v-for="(item, index) in videorelatedmsg.itemList" :key="index">
          <template v-if="item.type=='videoSmallCard'">
            <VideoSmallCard :item="item"></VideoSmallCard>
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VideoSmallCard from "@/components/VideoSmallCard.vue";

export default {
  components: {
    VideoSmallCard
  },
  data() {
    return {
      videorelatedmsg: {}
    };
  },
  computed: {
    currentPlay() {
      return this.$store.state.currentPlay;
    }
  },
  methods: {
    getTime(timestamp) {
      var time = new Date(timestamp);
      // var year = time.getFullYear();
      // var month = time.getMonth() + 1;
      // var date = time.getDate();
      var middle = time.getMinutes();
      var second = time.getSeconds();
      return middle + ":" + second;
    },
    getVideorelatedMsg() {
      this.axios
        .get(
          "http://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v4/video/related",
          {
            params: {
              id:
                this.currentPlay.data.id ||
                this.currentPlay.data.content.data.id
            }
          }
        )
        .then(response => {
          this.videorelatedmsg = response.data;
          // window.console.log(response.data);
        });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.getVideorelatedMsg();
    });
  }
};
</script>

<style lang="less" scoped>
.back {
  position: fixed;
  left: 2%;
  top: 2%;
  color: white;
  font-size: 36px;
  z-index: 999;
}
video {
  width: 100%;
  object-fit: cover;
  object-position: center center;
}
.play {
  .playbg {
    width: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    // background: rgba(206, 154, 154, 0.5);
    position: relative;
    &::after {
      content: "";
      width: 100%;
      height: 100%;
      background: rgba(0, 0, 0, 0.5);
      position: absolute;
      left: 0;
      top: 0;
      z-index: 2;
    }
    .content {
      position: relative;
      left: 0;
      top: 0;
      z-index: 3;
      .intro {
        padding: 0 5vw;
        h3 {
          font-size: 20px;
          font-weight: bold;
          color: white;
          margin-top: 10px;
        }
        p {
          color: #eee;
          margin: 10px 0 15px 0;
          span {
            margin-right: 5vw;
            font-size: 14px;
          }
        }
        h6 {
          margin: 15px 0;
          line-height: 1.2rem;
          color: #eee;
        }
      }
      .author {
        display: flex;
        padding: 5vw 5vw;
        border: 1px solid white;
        img {
          width: 50px;
          height: 50px;
          border-radius: 50%;
        }
        .text {
          flex: 1;
          margin-left: 5vw;
          h3 {
            line-height: 30px;
            font-weight: bold;
            display: -webkit-box;
            font-size: 14px;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            color: white;
          }
          p {
            line-height: 20px;
            font-size: 14px;
            color: gray;
            display: -webkit-box;
            font-size: 14px;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
            color: #eee;
          }
        }
        .btn {
          margin-top: 12px;
          border-radius: 10px;
          width: 50px;
          color: white;
          font-size: 12px;
          border: 1px solid white;
          height: 25px;
          background: transparent;
        }
      }
      .relate {
        color: white;
        p {
          span {
            color: white;
          }
        }
      }
    }
  }
}
</style>