<template>
  <div class="videmitem">
    <li>
      <div class="introduce">
        <img :src="item.data.content.data.author.icon" alt />
        <div class="text">
          <h5>{{item.data.content.data.author.name}}</h5>
          <p>
            <span>{{getTime(item.data.content.data.updateTime)}}发布：</span>
            <span>{{item.data.content.data.title}}</span>
          </p>
        </div>
      </div>
      <div class="explain">
        <h6>{{item.data.content.data.description}}</h6>
        <span v-for="(item, index) in item.data.content.data.tags" :key="index">{{item.name}}</span>
      </div>
      <div class="play">
        <a>
          <img :src="item.data.content.data.cover.detail" alt @click="routerToPlay"/>
        </a>
        <div class="totaltime">
          <span>{{computingTime(item.data.content.data.duration)}}</span>
        </div>
      </div>
    </li>
  </div>
</template>

<script>
export default {
  props: ["item"],

  methods: {
    getTime(timestamp) {
      var time = new Date(timestamp);
      var year = time.getFullYear();
      var month = time.getMonth() + 1;
      var date = time.getDate();
      return year + "年" + month + "月" + date + "日";
    },
    computingTime(time) {
      var m = parseInt(time / 60);
      var s = parseInt(time % 60);
      return m + ":" + s;
    },
    routerToPlay() {
      this.$router.push("/play");
      this.$store.commit("setCurrentPlay", this.item);
    }
  }
};
</script>

<style lang='less' scoped>
.videmitem {
  li {
    padding: 5vw;
    border: 1px solid lightgray;
    .introduce {
      display: flex;
      overflow: hidden;
      height: 50px;
      img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
      }
      .text {
        margin-left: 10px;
        flex: 1;
        h5 {
          font-size: 18px;
          font-weight: 600;
          margin-bottom: 5px;
        }
        p {
          display: -webkit-box;
          font-size: 14px;
          -webkit-line-clamp: 1;
          -webkit-box-orient: vertical;
          overflow: hidden;
          span {
            &:nth-child(1) {
              color: gray;
            }
            &:nth-child(2) {
              font-weight: bold;
            }
          }
        }
      }
    }
    .explain {
      margin-top: 1rem;
      h6 {
        height: 2.5rem;
        overflow: hidden;
      }
      span {
        display: inline-block;
        padding: 2px 8px;
        font-size: 14px;
        margin-top: 0.8em;
        margin-right: 1rem;
        color: blue;
        background: rgb(210, 232, 240);
        border-radius: 5px;
      }
    }
    .play {
      margin-top: 0.8em;
      position: relative;
      .totaltime {
        position: absolute;
        right: 3%;
        bottom: 5%;
        background: rgba(0, 0, 0, 0.7);
        color: white;
        padding: 2.5px 5px;
        font-size: 12px;
        border-radius: 5px;
      }
    }
  }
}
</style>