<template>
  <div class="week">
    <ul>
      <VideoList v-for="(item, index) in weekranking" :key="index" :item="item"></VideoList>
    </ul>
  </div>
</template>

<script>
import VideoList from "@/components/VideoList.vue";

export default {
  components: {
    VideoList
  },
  data() {
    return {
      weekranking: []
    };
  },
  methods: {
    getWeekRanking() {
      let weekranking = JSON.parse(window.localStorage.getItem("weekranking"));
      if (weekranking && Date.now() < weekranking.time) {
        this.weekranking = weekranking.data;
      }
      this.axios
        .get(
          "http://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v4/rankList/videos?strategy=weekly&amp;start=0&amp;num=10"
        )
        .then(response => {
          this.weekranking = response.data.itemList;
          // window.console.log(response.data);
          window.localStorage.setItem(
            "weekranking",
            JSON.stringify({
              time: Date.now() + 30 * 60 * 1000,
              data: this.weekranking
            })
          );
        });
    }
  },
  created() {
    this.getWeekRanking();
  }
};
</script>

