import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import HomeRanking from "../views/HomeRanking.vue";
import HomeRecommend from "../views/HomeRecommend.vue";
import HomeChannel from "../views/HomeChannel.vue";
import AllRank from "../views/AllRank.vue";
import Search from "../views/Search.vue";
import Play from "../views/Play.vue";


Vue.use(VueRouter);

const routes = [
	{
		path: "/",
		name: "home",
		component: Home,
		children: [
			{
				path: "ranking",
				component: HomeRanking
			},
			{
				path: "recommend",
				component: HomeRecommend
			},
			{
				path: "channel",
				component: HomeChannel
			}
		]
	},
	{
		path: "/channelcontent",
		name: "channelcontent",
		// route level code-splitting
		// this generates a separate chunk (about.[hash].js) for this route
		// which is lazy-loaded when the route is visited.
		component: () =>
			import(
				/* webpackChunkName: "about" */ "../views/ChannelContent.vue"
			)
	},
	{
		path: "/allrank",
		name: "allrank",
		component: AllRank
	},
	{
		path: "/search",
		name: "search",
		component: Search,
		
	},
	{
		path:"/play",
		name:"play",
		component:Play
	}
];

const router = new VueRouter({
	routes
});

export default router;
