<template>
  <div class="total">
    <ul>
      <VideoList v-for="(item, index) in totalranking" :key="index" :item="item"></VideoList>
    </ul>
  </div>
</template>

<script>
import VideoList from "@/components/VideoList.vue";

export default {
  components: {
    VideoList
  },
  data() {
    return {
      totalranking: []
    };
  },
  methods: {
    getTotalRanking() {
      let totalranking = JSON.parse(window.localStorage.getItem("totalranking"));
      if (totalranking && Date.now() < totalranking.time) {
        this.totalranking = totalranking.data;
      }
      this.axios
        .get(
          "http://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v4/rankList/videos?strategy=historical&amp;start=0&amp;num=10"
        )
        .then(response => {
          this.totalranking = response.data.itemList;
          window.console.log(response.data);
          window.localStorage.setItem(
            "totalranking",
            JSON.stringify({
              time: Date.now() + 30 * 60 * 1000,
              data: this.totalranking
            })
          );
        });
    }
  },
  created() {
    this.getTotalRanking();
  }
};
</script>

