<template>
  <div class="search">
    <van-sticky>
      <form action="/">
        <van-search
          v-model="value"
          placeholder="请输入搜索关键词"
          show-action
          @cancel="$router.go(-1)"
          @search="search"
        />
      </form>
    </van-sticky>

    <van-loading type="spinner" color="#1989fa" class="load" v-if="isloading" />

    <template v-if="isSearching">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        :error.sync="error"
        error-text="请求失败，点击重新加载"
        @load="onLoad"
        :immediate-check="false"
      >
        <van-cell id="cell" v-for="(item, index) in keywordmessage.itemList" :key="index">
          <template v-if="item.type=='textCard'">
            <KaiyanTitle :item="item"></KaiyanTitle>
          </template>

          <template v-if="item.type=='briefCard'">
            <BriefCard :item="item"></BriefCard>
          </template>

          <template v-if="item.type=='videoSmallCard'">
            <VideoSmallCard :item="item"></VideoSmallCard>
          </template>

          <template v-if="item.type=='followCard'">
            <VideoList :item="item"></VideoList>
          </template>
        </van-cell>
      </van-list>
    </template>

    <template v-else-if="!isSearching">
      <div>
        <h2>搜索历史</h2>
        <ul class="his">
          <li v-for="(item, index) in historyWords" :key="index">
            <p>{{item}}</p>
            <!-- <van-icon class="delate" name="cross"  /> -->
          </li>
        </ul>
        <h2>热搜关键词</h2>
        <ul>
          <li v-for="(hot, index) in hotwords" :key="index" @click="searchHotWord(hot)">{{hot}}</li>
        </ul>
      </div>
    </template>
  </div>
</template>

<script>
import KaiyanTitle from "@/components/KaiyanTitle.vue";
import VideoSmallCard from "@/components/VideoSmallCard.vue";
import BriefCard from "@/components/BriefCard.vue";
import VideoList from "@/components/VideoList.vue";

export default {
  components: {
    KaiyanTitle,
    VideoSmallCard,
    BriefCard,
    VideoList
  },
  data() {
    return {
      value: null,
      hotwords: {},
      keywordmessage: {},
      isSearching: false,
      isloading: false,
      nextPic: null,
      nextPageUrl: [],
      aa: null,
      loading: false,
      finished: false,
      error: false,
      history: []
    };
  },
  computed: {
    historyWords() {
      return JSON.parse(window.localStorage.getItem("history")).data;
    }
  },
  methods: {
    getHotWords() {
      this.axios
        .get(
          "http://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v3/queries/hot"
        )
        .then(response => {
          this.hotwords = response.data;
          window.console.log(response.data);
        });
    },
    getKeyWordMessage(word) {
      this.isloading = true;
      this.axios
        .get(
          "http://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v3/search?&vc=580&deviceModel=MI%20MAX%202",
          {
            params: {
              query: word
            }
          }
        )
        .then(response => {
          this.keywordmessage = response.data;
          this.nextPic = response.data.nextPageUrl;
          window.console.log(this.nextPic);
        })
        .finally(() => {
          this.isloading = false;
        });
    },
    search() {
      this.getKeyWordMessage(this.value);
      this.isSearching = true;
      this.history.push(this.value);
      window.console.log(this.history);

      window.localStorage.setItem(
        "history",
        JSON.stringify({ data: this.history })
      );
    },
    searchHotWord(hot) {
      this.value = hot;
      this.getKeyWordMessage(this.value);
      this.isSearching = true;
    },
    // scroll() {
    //   var that = this;
    //   this.aa = function() {
    //     var H = document.querySelector(".search").offsetHeight;
    //     var wh = this.innerHeight;
    //     var sh = this.pageYOffset;
    //     if (H - wh - sh < 1) {
    //       window.console.log("到底了");
    //       that.axios
    //         .get("http://api.kele8.cn/agent/" + that.nextPic)
    //         .then(response => {
    //           // this.nextPageUrl.push(response.data);
    //           that.nextPageUrl = response.data.itemList;
    //           // window.console.log(response);
    //           that.nextPic = response.data.nextPageUrl;
    //           that.nextPageUrl.forEach(element => {
    //             that.keywordmessage.itemList.push(element);
    //           });
    //         });
    //     }
    //   };
    //   window.addEventListener("scroll", this.aa);
    // },
    onLoad() {
      window.console.log("到底了");
      // window.console.log(this.nextPic);

      this.axios
        .get("http://api.kele8.cn/agent/" + this.nextPic)
        .then(response => {
          // this.nextPageUrl.push(response.data);
          this.nextPageUrl = response.data.itemList;
          this.loading = false;

          window.console.log(this.nextPic);
          this.nextPageUrl.forEach(element => {
            this.keywordmessage.itemList.push(element);
          });
          this.nextPic = response.data.nextPageUrl;
          if (!response.data.nextPageUrl) {
            this.finished = true;
          }
        })
        .catch(() => {
          this.error = true;
        });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.getHotWords();
      // vm.scroll();
    });
  }
};
//   beforeRouteLeave(to, from, next) {
//     window.removeEventListener("scroll", this.aa);
//     next();
//   }
// };
</script>

<style lang="less">
.search {
  .van-list {
    #cell {
      padding: 0;
    }
  }

  .load {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  h2 {
    font-size: 24px;
    font-weight: 600;
    margin-left: 5vw;
    margin-top: 30px;
    margin-bottom: 20px;
  }
  .van-list {
    .van-cell {
      padding: 0;
    }
  }
  ul {
    margin-left: 5vw;
    li {
      color: #4689d7;
      line-height: 3rem;
      border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    }
    &.his {
      li {
        display: flex;
        p {
          flex: 1;
        }
        .delate {
          line-height: 3rem;
          margin-right: 10px;
        }
      }
    }
  }
}
</style>